@import url(https://fonts.googleapis.com/css?family=Questrial&display=swap);
@import url(https://fonts.googleapis.com/css?family=Questrial&display=swap);
@import url(https://fonts.googleapis.com/css?family=Questrial&display=swap);
@import url(https://fonts.googleapis.com/css?family=Questrial&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}html{margin-left:calc(100vw - 100%)}body{max-width:1160px;margin:0 auto}.App{display:flex;min-height:100vh;flex-direction:column;text-align:center}.App main{width:100%;flex:1 1}

.home-container{height:calc(100vh - 13vh);width:100%;background-image:url(/static/media/project.52fda19a.gif);background-repeat:no-repeat;background-size:cover;display:flex;justify-content:center;align-items:center}.home-container .pitch{width:100%;padding:0 1em;font-family:"Questrial",Tahoma,sans-serif;font-size:2em;color:white}@media (min-width: 948px){.pitch{width:50%}}

.navigationItems{display:none}@media (min-width: 948px){.navigationItems{display:flex}.navigationItems ul{height:8vh;list-style:none;margin:0 1rem;padding:0;display:flex;font-family:"Questrial",Tahoma,sans-serif}.navigationItems .navigation-link{height:56px;display:flex;align-items:center;cursor:pointer}.navigationItems li{padding:0.5rem}.navigationItems a{color:#000000;text-decoration:none}.navigationItems a:hover{border-bottom:1px solid #069}.navigationItems a:active,.navigationItems a.active{background-color:#e7e6e6}}

.session-buttons{display:none}@media (min-width: 948px){.session-buttons{display:flex}.session-buttons ul{display:flex;height:8vh;list-style:none;margin:0 1rem;padding:0;font-family:"Questrial",Tahoma,sans-serif}.session-buttons .session-link{height:56px;display:flex;align-items:center;cursor:pointer}.session-buttons li{padding:0.5rem}.session-buttons a{color:#000000;text-decoration:none}.session-buttons a:hover{background-color:lightgray}.session-buttons a:active,.session-buttons a.active{border-bottom:1px solid #069}}

nav{width:100%;height:8vh;padding:0 1em;display:flex;justify-content:space-between;align-items:center;background-color:#f5f5f5}nav a{height:100%;display:flex;align-items:center}nav a img{height:75%}nav .searchBox{display:none}nav .burger-menu{display:block;cursor:pointer;font-size:1.5em;color:#069}@media (min-width: 948px){nav .spacer{flex:1 1}nav .otherLinks{display:flex;height:100%;align-items:center}nav .otherLinks .searchBox{display:flex}nav .otherLinks .search{position:relative;display:flex}nav .otherLinks .searchInput{background-color:transparent;background-image:url(/static/media/search_icon.de7a30de.svg);background-position:5px center;background-repeat:no-repeat;background-size:18px 18px;border:none;cursor:pointer;height:5vh;margin:0;padding:0 0 0 34px;position:relative;-webkit-transition:width 400ms ease, 400ms ease;transition:width 400ms ease, 400ms ease;left:0;width:0;font-family:"Questrial",Tahoma,sans-serif;font-size:.85em;border-radius:5px}nav .otherLinks .searchInput:hover{background-color:white;cursor:text;outline:0;width:200px}nav .otherLinks .searchInput:focus{background-color:white;cursor:text;outline:0;width:200px}nav .burger-menu{display:none}}

.footer{width:100%;height:5vh;background-color:whitesmoke;font-size:1em;display:flex;justify-content:center;align-items:center}

.league-card{width:12em;margin:1em;cursor:pointer}.league-card img{width:100%}

.card-container{width:100%;padding:0 5em;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center}

.profilePic{width:10em}

.league-card{width:12em;margin:1em;cursor:pointer}.league-card img{width:100%}

.card-container{margin-top:1em}.card-container .singleLeague-badge{width:10em}

